.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


section.route-section {
  display: block;
  /* position: relative; */
  margin: 100px auto;
 
}


.switch-enter {
  display: inline;
  opacity: 0;
  transform: translateY(-1000px);

}
.switch-enter-active {
  display: inline;
  opacity: 1;
  transform: translateY(0);
  transition: opacity 1000ms, transform 1000ms;
}
.switch-exit {
  display: inline;
  transform: translateY(0px);
  opacity: 1;
}
.switch-exit-active {
  display: inline;
  opacity: 0;
  transform: translateY(1000px);
  transition: opacity 1000ms, transform 1000ms;
}

img {
  display: block;
  margin: 0 auto;
}


h1 {
  /* font-size: 160px; */
  text-align: center;
}

.homeP {
  font-size: 40px;
  text-align: center;
  width: 70%;
  margin: 30px auto;
}

.worklist {
  font-size: 14px;
  text-align: left;
  margin-left: 40px;
}

h5 {
  text-align: left;
  
}
